
import dateFormat from 'vux/src/tools/date/format.js'

import '../../assets/js/clipboard'
import api from '../../assets/js/api'
import {getParams,weChatPay} from '../../assets/js/util'
export default {
components: {},
filters: {
dateFormat
},
data () {
return {
wuliu: '1231231231',
orderId: getParams()['oreder_id'] || '',
detail: {
"id": 1,
"ordersn": "1232324343",
"total": "99999",
"amount": 88,
"freight": "123123",
"rebate": "11",
"delivery": 2,
"province": "",
"city": "",
"district": "",
"address": "",
"username": "",
"phone": 1231232,
"addTime": 1,
"payType": 1,
"payTime": 13412343243,
"deliveryTime": 13412343243,
"finishTime": 13412343243,
"good": [
{
"good_name": "",
"good_price": "88",
"good_photo": null,
"rebate": "2",
"amount": 1
}
],
"condition": "",
"logistics": [
{


}
],
"makerAddress": [
{
"phone": 12332432,
"province": "",
"city": "",
"district": "",
"address": ""
}
]
}
}
},
methods: {
layer(text){
this.$vux.toast.text(text || 'hello', 'middle')
},
showLoading(){
this.$vux.loading.show({
text: ''
})
},
hideLoading(){
this.$vux.loading.hide()
},
copy(){
var clipboard = new Clipboard('button');
clipboard.on('success', e => {

this.layer('');
});
clipboard.on('error', e => {

this.layer('');
});
},

changeOrderStatus(status){
this.showLoading('')
this.$http.post(api.changeOrderStatus,{
ordersn: this.detail.ordersn,
status: status
}).then(res => {
this.hideLoading()
this.layer('')
}).catch(e => {
this.hideLoading()
})
},
gotoViewCer(){
this.$router.push({
path: `/viewcer?id=`
})
},
/**/
gotoWeChatPay(){
location.href = weChatPay(this.detail.ordersn)
},
/**/
uploadVoucher(){
this.$router.push({
path: `/offinePay?order_id=${this.detail.ordersn}&isPost=${this.detail.delivery}`
})
},
/**/
passVerify(type){
this.showLoading('')
this.$http.post(api.verify,{
ordersn: this.detail.ordersn,
type: type
}).then(res => {
this.hideLoading()
this.layer('')
/* todo: */
}).catch(e => {
this.hideLoading()
})
},
},
mounted() {
this.copy();

document.getElementsByTagName('title')[0].textContent = '';
}
}



