
import XInput from 'vux/src/components/x-input/index.vue'
import Countdown from 'vux/src/components/countdown/index.vue'
import Previewer from 'vux/src/components/previewer/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

import api from '../../assets/js/api'
import UploadPhoto from '../../components/uploadPhoto.vue'
import {getParams} from '../../assets/js/util'
export default {
directives: {
TransferDom
},
components: {
XInput,
Countdown,
UploadPhoto,
Previewer
},
data () {
return {
name: '',
phone: '',
code: '',
password: '',
rePassword: '',
time: 90,
start: true,
isSendCoding: false,
list: [
{
msrc: 'http://ww1.sinaimg.cn/thumbnail/663d3650gy1fplwu9ze86j20m80b40t2.jpg',
src: 'http://ww1.sinaimg.cn/large/663d3650gy1fplwu9ze86j20m80b40t2.jpg',
},
{
msrc: 'http://ww1.sinaimg.cn/thumbnail/663d3650gy1fplwvqwuoaj20xc0p0t9s.jpg',
src: 'http://ww1.sinaimg.cn/large/663d3650gy1fplwvqwuoaj20xc0p0t9s.jpg',
}, {
msrc: 'http://ww1.sinaimg.cn/thumbnail/663d3650gy1fplwwcynw2j20p00b4js9.jpg',
src: 'http://ww1.sinaimg.cn/large/663d3650gy1fplwwcynw2j20p00b4js9.jpg'
}
],
id: getParams()['id'] || '',
tjname: getParams()['name'] || '',
}
},
methods: {
layer(text){
this.$vux.toast.text(text || 'hello', 'middle')
},
showLoading(){
this.$vux.loading.show({
text: ''
})
},
hideLoading(){
this.$vux.loading.hide()
},
finish(){
this.isSendCoding = false;
this.time = 90;
},
sendCode(){
if (!this.$refs.phoneInput.valid) {
this.layer('')
} else {
this.$http.post(api.getCode, {
event: 'register',
mobile: this.phone
}).then(res => {
this.isSendCoding = true;
})
}
},
chooseImg(){
/* fastClickbug */
this.$refs['uploadPhoto'].$el.click();
this.$refs['uploadPhoto'].$el.click();
},

getImgDataUrl(value){

alert(value);
},
uploadProgress(value){


},
uploadResData(value){
if (value.type == 'success') {
let img = new Image();
let that = this;
img.src = value.image_url;

img.onload = function () {

}
} else {

}
},
show (index) {
this.$refs.previewer.show(index)
},
deletePhoto (index){
this.list.splice(index, 1);
},
submit(){
if (!this.$refs.nameInput.valid) {
this.layer('')
} else if (!this.$refs.phoneInput.valid) {
this.layer('')
} else if (!this.$refs.codeInput.valid) {
this.layer('')
} else if (!this.$refs.pwdInput.valid) {
this.layer('')
} else if (!this.$refs.rePwdInput.valid) {
this.layer('')
} else if (this.list.length <= 0) {
this.layer('')
} else {
this.showLoading('')
this.$http.post(api.registerMaker,{
makerId: this.id,
realname: this.name,
phone: this.phone,
password: this.password,
contract: '2343434534545'
}).then(res => {
this.hideLoading()
this.layer('')
this.$router.push({
path: '/'
})
}).catch(e =>{
this.hideLoading()
})
}
}
},
mounted() {

document.getElementsByTagName('title')[0].textContent = '';
}
}



